.projects {
  margin-top: -6.4rem;
  position: relative;
  background-color: $color-gray-light-2;
  height: 130vh;

  @include respond(phone){
    height: 95vh;
  }

  &-heading__container {
    @include respond(phone) {
      margin-left: -10%;
    }
  }

  &__background {
    height: 100%;
    width: 100%;
    clip-path: polygon(0 0, 100% 0, 100% 60%, 50% 100%, 0 100%);
    background-image: linear-gradient(to top right, $color-blue-dark, $color-blue-light);

    @include respond(phone){
      clip-path: polygon(0 0, 100% 0, 100% 85%, 50% 100%, 0 100%);
    }
  }

  &__container {
    // width: 80%;
    background-color: $color-white;
    border-radius: 5px;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.4);
    padding: 6rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -60%);
    width: 35%;
    min-height: 54.6rem;

    @include respond(phone) {
      width: 80%;
      top: 60%;
    }
  }

  &__list {
    list-style: none;

    &--animated {
      animation: fadeIn .5s ease-in .75s;
      animation-fill-mode: backwards;
    }
  }

  &__link {
    &:link,
    &:visited {
      font-size: 1.3rem;
      text-decoration: none;
      // text-transform: uppercase;
      display: inline-block;
      transition: all .2s;
      color: $color-gray-dark-1;
    }

    &:hover,
    &:active {
      transform: scale(1.5);
      color: $color-gray-dark-3;
      // margin-top: 1rem;
      // margin-bottom: 1rem;
    }
  }
}